import  React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import QATable from "./Table";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
    },
}));

export default function SMSInbox (props) {
    const classes = useStyles();
    const { inboxData, isLoading } = props;
    const [inbox_data, setInboxData] = useState([]);

    const columns = [
        {
            name: "inbox_link",
            label: "Chatterbox link",
            options: {
                filter: false,
                sort: false
            }

        },
        {
            name: "timestamp",
            label: "Timestamp",
            options: {
                filter: true,
                sort: true,
            }
        },
        {
            name: "site_code",
            label: "Site Code",
            options: {
                filter: true,
                sort: true
            }

        },
        {
            name: "affiliation",
            label: "Affiliation",
            options: {
                filter: false,
                sort: false
            }

        },
        {
            name: "sender",
            label: "Sender",
            options: {
                filter: false,
                sort: false
            }

        },
        {
            name: "message",
            label: "Message",
            options: {
                filter: false,
                sort: false
            }
        },
        {
            name: "tag",
            label: "Tag",
            options: {
                filter: false,
                sort: false
            }
        },
    ];

    const generateChatterboxLink = (mobile_id) => {
        return (
            <a href={`/communication/chatterbox/${mobile_id}`}>{mobile_id}</a>
        )
    }

    useEffect (()=> {
        if (inboxData) {
            const updated_inbox_data = inboxData.map((row, index)=> {
                const {mobile_id} = row;
                row.inbox_link = generateChatterboxLink(mobile_id);
                return row;
            })
           setInboxData(updated_inbox_data);
        }
    },[])


    return (
        <div className={classes.root}>
            <QATable 
                isLoading={isLoading} 
                tableTitle="QA for Event Monitoring" 
                type="SMS Inbox"
                datas={inbox_data}
                columns={columns}
            />
        </div>
    );
}